@import "lib";

.partners {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  width: 100%;

  @include tablet-min-width {
    width: 33%;
    gap: 23px;
  }
}

.partners__link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  margin-top: 5px;
  gap: 23px;
  width: 100%;

  text-decoration: none;
  color: $color-black-dark;
  word-break: break-word;

  @include tablet-min-width {
    justify-content: flex-end;
    line-height: 20px;
    margin-top: 0;
  }
}

.partners__logo {
  display: flex;
  flex-shrink: 0;
}

.partners__icon {
  width: 27px;
  height: 34px;
}

.partners__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 23px;
  width: 100%;

  @include tablet-min-width {
    justify-content: flex-end;
  }
}
